.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    font-family: Arial, sans-serif;
    background: #f4f4f4;
    height: 100vh;
}

h1, h2 {
    color: #333;
}

.form-container {
    background: white;
    padding: 60px;
    border-radius: 40px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    width: 320px; /* 컨테이너 너비 유지 */
    text-align: center;
}

label {
    margin: 0 10px;
}

input[type="text"],
input[type="password"],
select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px; /* 각 입력란 사이의 간격을 줄임 */
}

button {
    width: auto; /* 버튼 너비 자동 설정 */
    padding: 10px 20px; /* 버튼 패딩 */
    background-color: #8b0b02; /* 버튼 색상 */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    margin-bottom: 5px; /* 버튼 간격 */
}

button:hover {
    background-color: #6c0a01; /* 버튼 호버 색상 */
}

